import React from 'react';

///styles
import styles from './LoadingAlert.module.scss';

/**@description stateless; at the absolute position it includes <img>
 * with 'loading' icon;
 * */
const LoadingAlert = () => {
    return (
        <div className={styles.loadContainer}>
            <img alt="" src="data:image/gif;base64," />
        </div>
    );
};
export default LoadingAlert;